import React, {useState, useEffect, useRef} from 'react';
import TableTest from "../../components/TableTest";
import 'bootstrap/dist/css/bootstrap.css';
import {Link} from "react-router-dom";
import {Cookie} from "react-cookie";

import Pagination from "react-js-pagination";

function QnaBoard() {
    const [qnaList, setQnaList] = useState([]);
    const [totalElements, setTotalElements] = useState(0);

    const [page, setPage] = useState(1);
    const [isCheckingBox, setIsCheckingBox] = useState(false)

    // TODO :       .
    const id = "user2";

    const checkingCheckedBox = (e) => {
        if (e.target.checked) {
            setIsCheckingBox(true);
        } else if(!e.target.checked) {
            setIsCheckingBox(false);
        }
    }

    const handlePageChange = (page) => {
        setPage(page);
    };

    useEffect(() => {
        if(isCheckingBox) {
            const getData = async () => {
                const res = await fetch(`/qna/v1/qnaByWriter?page=${page}&writer=${id}`, {
                    headers: {
                        Authentication: 'Bearer '+localStorage.getItem("auth_token")
                    }
                });
                const data = await res.json();
                console.log("   data", data)
                setQnaList(data);
                setTotalElements(data.totalElements);
            }
            getData();
        } else {
            const getData = async () => {
                const res = await fetch(`/qna/v1/qnabylist?page=${page}`, {
                    headers: {
                        Authentication: 'Bearer '+localStorage.getItem("auth_token")
                    }
                });
                console.log(id);
                const data = await res.json();
                console.log("   data", data)
                setQnaList(data);
                setTotalElements(data.totalElements);
            }
            getData();
        }
    }, [isCheckingBox, page])

    // useEffect(() => {
    //     const getData = async () => {
    //         const res = await fetch(`/qna/v1/qnabylist?page=${page}`);
    //         const data = await res.json();
    //         setQnaList(data);
    //         setTotalElements(data.totalElements);
    //     }
    //     getData();
    // }, [page])

    return (

        <div>
            <div align="center" style={{padding : 100}}>
                <div>
                    <h1> </h1>
                </div>

                <TableTest data={qnaList} />
                <div align="right">
                    <button className="btn btn-warning" style={{fontWeight: "bold", color: "white", backgroundColor: "#217Af0", width: 100}}>
                        <Link to={"/qnawrite"} style={{color: "white"}}>
                        
                        </Link>
                    </button>
                </div>
                <div align="center" >
                {/*<Label><input type="checkbox" />       .</Label>*/}
                    <input type="checkbox" style={{margin: 3, zoom: 1.5}} onClick={checkingCheckedBox}  />
                    <label style={{margin: 3 }}>       .</label>
                </div>
                <Pagination
                    activePage={page} //  
                    itemsCountPerPage={10} //      
                    totalItemsCount={totalElements} //   
                    pageRangeDisplayed={5} // paginator  
                    prevPageText={""} // ""  
                    nextPageText={""} // ""  
                    onChange={handlePageChange} //    
                />
            </div>
        </div>
    )
}

export default QnaBoard;
