import React, {useState,useEffect} from "react";
import {useRef} from "react";
import {useNavigate} from "react-router-dom";
import axios from "axios";

// TODO : 2022.11.17  ->  (List)  (    tr  .), post Test . ONLY DTO Test .
function ItemWrite() {

    /*
        input Box Ref
     */
    const titleRef = useRef(null);
    const contentRef = useRef(null);
    const navigate = useNavigate(); //navigate

    const [images, setImages] = useState([]);
    const imageList = [];
    let now = null;
    const onAddImages = (e) => {
        // setImages(e.target.files[0]);
        now = e.target.files;
        console.log(" : " + e.target.files.length);
        for(let i=0; i <= e.target.files.length; i++){
            imageList.push(now[i]);
        }

        // console.log("  : " + now.length);
        // for (let i = 0; i < now.length; i +=1) {
        //     // imageList.push(now[i]);
        //     setImages(now[i]);
        //     console.log("  : " + images[i]);
        // }
    }

    /*
        select Box
     */
    const[select, setSelect] = React.useState('');
    const onChangeSelect = (e) => {
        setSelect(e.target.value);
    }

    /*
        post  - 
     */
    const onClick = () => {
        const titleVal = titleRef.current.value;
        const contentVal = contentRef.current.value;

        // 
        if(titleVal === '') {
            window.alert('  .');
            return;
        }else if(select === '' || select === 'none') {
            window.alert('   .');
            return;
        }else if(contentVal === '') {
            window.alert('  .');
            return;
        }else if(select === '') {
            const confirm = window.confirm('   .  ?');
            if(confirm === false) {
                return;
            }
        }

        // axios 
        // TODO : writer   ||  ID  
        const frm = new FormData();
        const data = {
            title: titleVal,
            content: contentVal,
            writer: "iu1234",
            type: select};
        frm.append("dto", new Blob([JSON.stringify(data)],  {type: "application/json"}));
        // frm.append("files", imageList.values());
        imageList.forEach((file) => {
            //   
            frm.append('files', file);
        });

        //application/json , multipart/form-data
        axios.post("/item/v1/savePost/images", frm, {
            headers: {
                "Content-Type": `multipart/form-data`,
            }})
            .then(function (response) {
                // response
                console.log('res : ' + response.status); // 200
                window.alert(" !");
                navigate('/item/itemBoard');
            }).catch(function (error) {
            //  
            console.log('error message : '+ error);
            window.alert("  .    .");
        }); // axios
    }// onClick


    /*
        return .
     */
    return(
        <div>
            <div align="center" style={{padding : 100}}>
                <div >
                    <h1> </h1>
                </div>
                    <table className="table table-striped table-bordered table-hover">
                        <tbody>
                        <tr>
                            {/*TODO : td        */}
                            <th scope="row" ></th>
                            <td colSpan="6">iu1234</td>
                        </tr>
                        <tr>
                            <th scope="row" ></th>
                                <td colSpan="3" width="25%">
                                    <input ref={titleRef} type="text" placeholder=" "/>
                                </td>
                            <th scope="row" width="6%"> </th>
                                <td>
                                    <select value={select} onChange={onChangeSelect}>
                                        <option value='none'>==  ==</option>
                                        <option value=''></option>
                                        <option value=''></option>
                                    </select>
                                </td>
                        </tr>
                        <tr>
                            <th scope="row"> </th>
                            <td colSpan="5">
                                <input type="file" multiple="multiple" onChange={onAddImages} />
                            </td>
                        </tr>
                        <tr>
                            <th scope="row"></th>
                                <td colSpan="6" className="view_text">
                                    <textarea ref={contentRef} style={{width: 800, height: 300}} placeholder=" . (1000)"></textarea>
                                </td>
                        </tr>
                        </tbody>
                    </table>
                    <div align="right">
                        <button onClick={onClick} className="btn btn-warning" style={{backgroundColor: "#217Af0", width: 100}} >
                             
                        </button>
                    </div>
            </div>

        </div>
    )
}//func

export default ItemWrite;