import {useRef} from "react";
import {useNavigate} from "react-router-dom";


function QnaWrite() {

    const titleRef = useRef(null);
    const contentRef = useRef(null);
    const pwdRef = useRef(null);
    const navigate = useNavigate();

    function onSubmit(e) {

        e.preventDefault();

        const titleVal = titleRef.current.value;
        const contentVal = contentRef.current.value;
        const pwdVal = pwdRef.current.value;


        const inputPattern = /^\s+|\s+$/g;
        if(titleVal.replace(inputPattern, '' ) === "" ){
            titleRef.current.focus();
            alert('  .');
                return;
        } else if(contentVal.replace(inputPattern, '' ) === "") {
            contentRef.current.focus();
            alert('  .');
                return;
        } else if(pwdVal.replace(inputPattern, '' ) === "") {
            pwdRef.current.focus();
            alert(' ');
                return;
        } else if (pwdVal.length < 4 || pwdVal.length > 12) {
            pwdRef.current.focus();
            alert(' 4 12  ');
                return;
        }

        const uri = `http://localhost:8080/qna/v1/qna`;
        const encoded = encodeURI(uri);
        fetch(encoded, {
            method: "PUT",
            headers: {
                "Content-type": "application/json",
            },
            body: JSON.stringify({
                title: titleVal,
                content: contentVal,
                writer: "user2",
                pwd: pwdVal
            }),
        }).then(res => {
            if(res.ok) {
                navigate('/qna/qnaBoard');
            } else {
                alert("  .    ");
            }
        })
    }

    return (
        <div>
            <div align="center" style={{padding : 100}}>
                <div >
                    <h1> qna </h1>
                </div>
                <form onSubmit={onSubmit}>
                    <table className="table table-striped table-bordered table-hover" >

                        <tbody>
                        <tr>
                            <th scope="row"></th>
                            <td >xodlf5363</td>
                        </tr>
                        <tr>
                            <th scope="row" ></th>
                            <td colSpan="3">
                                <input type="text" id={"title"} ref={titleRef} placeholder=" "/>
                            </td>
                        </tr>
                        <tr>
                            <th scope="row"></th>
                            <td colSpan="4" className="view_text">
                                <textarea ref={contentRef} style={{width: 500, height: 200}} placeholder=" "></textarea>
                            </td>
                        </tr>
                        <tr>
                            <th scope="row"></th>
                            <td colSpan="4" className="view_text">
                                <input type="password" ref={pwdRef} placeholder="" />
                            </td>
                        </tr>
                        </tbody>
                    </table>
                    <div align="right">
                        <button className="btn btn-warning" style={{backgroundColor: "#217Af0", width: 100}} ></button>
                    </div>
                </form>
            </div>

        </div>
    )
}
export default QnaWrite;