import {useEffect, useRef, useState} from "react";
import {useNavigate, useParams} from "react-router-dom";
import axios from "axios";


function QnaPwdChk() {
    const {qnaId} = useParams();
    const pwdRef = useRef(null);
    const navigate = useNavigate();

    const chkPwd = async () => {
        const pwd = pwdRef.current.value;
        try {
            const inputPattern = /^\s+|\s+$/g;
            if(pwd.replace(inputPattern, '' ) === "" ) {
               pwdRef.current.focus();
                   alert('  .');
                       return;
            } else if (pwd.length < 4 || pwd.length > 12) {
                pwdRef.current.focus();
                    alert(' 4 12  ');
                        return;
            }

            const chk = await axios.get(`/qna/v1/confirmpwd`, {
                params: { qnaId, pwd }
            });
            if(chk.data === 1) {
                navigate(`/qnaDetail/${qnaId}`);
            } else if (chk.data === 0) {
                alert("  .");
            }

        } catch (err) {
            console.log("   .    .");
        };
    }

    return (
        <div align="center" style={{padding : 100}}>
        <h1>  </h1>
            <br />
            <input type="password" ref={pwdRef} placeholder="  ( 12)" className="size" maxLength={12} style={{border: 30 }} autoFocus/>
            <br />
            <br />
            <button className="btn btn-warning" onClick={chkPwd} style={{backgroundColor: "#217Af0", color: "white"}}>   </button>
            <br />

        </div>
    )
}

export default QnaPwdChk;