import React,{useRef, useState} from "react";
import {useNavigate, useParams} from "react-router-dom";
import axios from "axios";

function ApplyWrite() {
    // param : boardID, writer(String : server  Long .)
    const {id, writer} = useParams(); //param
    const titleRef = useRef(null);
    const contentRef = useRef(null);
    const writerRef = useRef(null);
    const writerEmailRef = useRef(null);
    const applyType = useRef(null);

    const navigate = useNavigate();

    // .
    const[select, setSelect] = React.useState('');
    const onChangeSelect = (e) => {
        setSelect(e.target.value);
    }

    // click event
    const onClickSubmit = () => {
        const confirm = window.confirm("  . ?");
        if(confirm === false){
            return;
        }

        const memberId = writer;//  
        const boardId = id; // 
        const applyWriter = writerRef.current.value; //  ID
        const applyWriterEmail = writerEmailRef.current.value; //  email
        const titleVal = titleRef.current.value; // title
        const contentVal = contentRef.current.value; // 
        const skillLevelVal = select; // 
        const applyTypeVal = applyType.current.value; // 

        // memberId : ->  String  Request,   ID .
        axios.post('/apply/v1/save', {
            title: titleVal,
            type: applyTypeVal,
            level: skillLevelVal,
            writer: applyWriter,
            writerEmail: applyWriterEmail,
            content: contentVal,
            itemBoard: boardId,
            memberId: memberId,
            applyStatus: 'none'
        }).then(function (res){
            window.alert("  .");
            navigate('/itemDetail/' + id);
        }).catch(function (err) {
            window.alert("   .   .");
        })
    }

    return(
        <div>
            <div align="center" style={{padding : 100}}>
                <div >
                    <h1> </h1>
                </div>
                <table className="table table-striped table-bordered table-hover">
                    <tbody>
                    <tr>
                        {/*TODO : td        */}
                        <th scope="row" > ID</th>
                        {/*<td colSpan="6">iu1234</td>*/}
                        <td colSpan='6'>
                            <input ref={writerRef} type="text" style={{width:200}}/>
                        </td>
                    </tr>
                    <tr>
                        <th scope="row" ></th>
                        <td colSpan="3" width="23%">
                            <input ref={titleRef} type="text" placeholder=" " style={{width:300}}/>
                        </td>
                        <th scope="row" width="10%"> </th>
                        <td>
                            <select value={select} onChange={onChangeSelect}>
                                <option value='none'>==  ==</option>
                                <option value='beginner'>(1)</option>
                                <option value='junior'>(1~3)</option>
                                <option value='senior'>(4~8)</option>
                                <option value='master'>(9 )</option>
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <th scope="row"> </th>
                        <td colSpan="3">
                            <input type="text" ref={writerEmailRef} style={{width: 300}}  placeholder="   ." />
                        </td>
                        <th scope="row"></th>
                        <td>
                            <input type="text" ref={applyType} placeholder='ex) , , ' style={{width:300}}/>
                        </td>
                    </tr>
                    <tr>
                        <th scope="row"></th>
                        <td colSpan="6" className="view_text">
                            <textarea ref={contentRef} style={{width: 800, height: 300}} maxLength={1000} placeholder=" . (1000)"></textarea>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </div>
            <div align="center">
                <button onClick={onClickSubmit} className="btn btn-warning" style={{backgroundColor: "#217Af0", width: 380, fontSize: 22}} >
                     
                </button>
            </div>
        </div>
    )

}//func

export default ApplyWrite;
