import React, {useState, useEffect} from "react";
import {Link} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.css';
import {v4} from 'uuid';
import axios from "axios";

import Paging from "../../components/Paging";
import TableTest from "../../components/TableTest";
import Pagination from "react-js-pagination";

function ItemBoard() {
    const [itemList, setItemList] = React.useState([]); // post
    const [page, setPage] = React.useState(1); // page Data
    let total = 0;

    // paging handler
    const handlePageChange = (page) => {
        setPage(page);
    };

    // use Effect
    useEffect(() => {
    const getData = async () => {
        try {
            const item = await axios.get(`/item/v1/findAllPage`, {
                params: {page}
            });
            setItemList(item.data);
            console.log(itemList);
        } catch (err) {
            console.log(err);
        };
    }
        getData();
    }, [page])//use eff

    /*
         total count   .
     */
    total = itemList.totalElements;
    total *= 1;

    /*
        
     */
    // drop down
    const [select, setSelect] = React.useState(''); //   : title, writer
    const onChangeSelect = (e) => {
        setSelect(e.target.value);
        // console.log(e.target.value);
        // console.log(select);
    }
    /*
         
     */
    const [keyWord, setKeyWord] = React.useState(""); //   
    const onChangeKeyWord = (e) => {
        setKeyWord(e.target.value);
    }
    /*
          .
     */
    const onClickBtn = () => {
        if(select === '' || select === 'none'){
            window.alert("   .");
            return;
        } else if(keyWord == "") {
            window.alert("  .");
            return;
        } else if(keyWord == " ") {
            window.alert("  .");
            return;
        }

        const getSearch = async () => {
            try {
                const searchResult = await axios.get('/item/v1/findTitleOrWriter', {
                    params: {
                        keyType: select,
                        keyWord: keyWord
                    }
                });
                setItemList(searchResult.data);
                console.log(" : " + itemList);
            }catch (e) {
                console.log(e);
            };
        }//get search
        getSearch();
    }//btn

    /*
             
     */
    const onKeyPress = (e) => {
        if(e.key == 'Enter') {
            onClickBtn();
        }
    } // enter press

    return (
        <div>
            <div align="center" style={{padding : 100, paddingRight: 330, paddingLeft: 330}}>
                <div>
                    <h1> </h1>
                </div>

                {/**/}
                <div>
                    <table className="table table-striped table-bordered table-hover">
                        <thead>
                        <tr>
                            <th scope="col"> </th>
                            <th scope="col"></th>
                            <th scope="col"></th>
                            <th scope="col"> </th>
                            <th scope="col"> </th>
                            <th scope="col"></th>
                        </tr>
                        </thead>
                        <tbody>
                        {itemList.content && itemList.content.map(data => (
                            <tr key={v4()}>
                                <td>
                                    {data.id}
                                </td>
                                <td>
                                    <Link to={`/itemDetail/${data.id}`}> {data.title} </Link>
                                </td>
                                <td>
                                    {data.writer}
                                </td>

                                {/*   IF  */}
                                {
                                    data.type === ''
                                    ?
                                    <td style={{color:"green"}}>
                                        {data.type}
                                    </td>
                                        :<td style={{color:"red"}}>
                                            {data.type}
                                        </td>
                                }
                                <td>
                                    {data.createdDate}
                                </td>
                                <td>
                                    {data.view}
                                </td>
                            </tr> ))}
                        </tbody>
                    </table>
                </div>

                <div align="right" style={{paddingBottom:50}}>
                    <button className="btn btn-warning" style={{fontWeight: "bold", color: "white", backgroundColor: "#217Af0", width: 100}}>
                        <Link to={"/itemWrite"} style={{color: "white"}}>
                            
                        </Link>
                    </button>
                </div>

                <div align="center">
                    <select value={select} onChange={onChangeSelect} className="size num1" style={{width:120}}>
                        <option value='none'>==  ==</option>
                        <option value='title'></option>
                        <option value='writer'></option>
                    </select>

                    <input id="keyWord" type="text" placeholder="   ." onChange={onChangeKeyWord} onKeyPress={onKeyPress} className="size" style={{width:350}}/>
                    <button onClick={onClickBtn} className="btn btn-warning" style={{fontWeight: "bold", color: "white", backgroundColor: "#217Af0", width: 100}}>
                        
                    </button>
                </div>

                {/*  paging  */}
                <Pagination
                    activePage={page} //  
                    itemsCountPerPage={10} //      
                    totalItemsCount={total} //   
                    pageRangeDisplayed={5} // paginator  
                    prevPageText={""} // ""  
                    nextPageText={""} // ""  
                    onChange={handlePageChange} //    
                />
            </div>
        </div>
    )//return
}// func

export default ItemBoard;