import {useParams} from "react-router-dom";
import {useEffect, useState, useRef} from "react";
import {useNavigate} from "react-router-dom";
import axios from "axios";
import React from "react";

function ItemBoardUpdate() {
    const {id, title, writer, content, createdDate} = useParams(); //param
    const titleRef = useRef(null);
    const contentRef = useRef(null);
    const navigate = useNavigate();
    let date = "";

    // 
    const[select, setSelect] = React.useState('');
    const onChangeSelect = (e) => {
        setSelect(e.target.value);
    }

    const onClickUpdate = () => {
        const titleVal = titleRef.current.value;
        const contentVal = contentRef.current.value;
        const idVal = id;

        const inputPattern = /^\s+|\s+$/g;
        if(titleVal.replace(inputPattern, '' ) === "" ){
            alert('  .');
            return;
        } else if(contentVal.replace(inputPattern, '' ) === "") {
            alert('  .')
            return;
        } else if(select === '' || select === 'none') {
            window.alert('  !');
            return;
        }

        axios.put('/item/v1/updatePost', {
                id: idVal,
                title: titleVal,
                content: contentVal,
                type: select
        }).then(function (res) {
            window.alert('  ');
            navigate('/item/itemBoard');
        }).catch(function (err){
            console.log(err);
            window.alert('   .');
        })
    }




    return (
        <div>
            {/*<div align="center" style={{padding : 100, paddingRight: 330, paddingLeft: 330, }}>*/}
            <div align="center" style={{padding : 100}}>
                <div >
                    <h1>  </h1>
                </div>
                    <table className="table table-striped table-bordered table-hover">
                        <tbody>
                        <tr>
                            <th scope="row"> </th>
                            <td colSpan="4">{id}</td>

                        </tr>
                        <tr>
                            <th scope="row"></th>
                            <td colSpan="4">{date = createdDate.substring(0, 10)}</td>
                        </tr>
                        <tr>
                            <th scope="row"></th>
                            <td colSpan="4" >{writer}</td>

                        </tr>
                        <tr>
                            <th scope="row"></th>
                            <td colSpan="0" style={{width:500}}>
                                <input type="text" id="title" name="title" defaultValue={title} ref={titleRef} style={{width: 500}} />
                            </td>

                            <th scope="row"></th>
                            <td>
                                <select value={select} onChange={onChangeSelect}>
                                    <option value='none'>==  ==</option>
                                    <option value=''></option>
                                    <option value=''></option>
                                </select>
                            </td>
                        </tr>
                        <tr>
                            <th scope="row"></th>
                            <td colSpan="4" className="view_text">
                                <textarea style={{width: 500, height: 200}} ref={contentRef}  title=""  name="contents" defaultValue={content}></textarea>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                    <div align="right">
                        <button className="btn btn-warning" style={{backgroundColor: "#217Af0", width: 100}} onClick={() => navigate(-1)}>   </button>
                        <button className="btn btn-warning" style={{backgroundColor: "#217Af0", width: 100}} onClick={onClickUpdate}>  </button>
                    </div>
            </div>
        </div>
    )
}

export default ItemBoardUpdate;