import React, {useRef, useState} from "react";
import {Link, useParams} from "react-router-dom";
import {useNavigate} from "react-router-dom";
import axios from "axios";
import Loader from "../../components/Loader";


function ApplyAccept() {


    const navigate = useNavigate();
    // parameters
    const {applyId} = useParams();
    const {writer} = useParams();
    const {writerEmail} = useParams();
    const {title} = useParams();
    const {itemBoard} = useParams();

    // ref
    const contentRef = useRef(null); //
    const titleRef = useRef(null);
    const writerRef = useRef(null);

    // Loading
    const [loading, setLoading] = useState(false);
    if(loading) {
        return <Loader type='spin' color='blue' message={"  ."}/>;
    }


    const onClickApply = () => {
        const con = window.confirm("  ?");
        if(con === false){
            return;
        }

        // loading
        setLoading(true);

        const data = {
            writerEmail: writerEmail,
            content: contentRef.current.value,
            title: title,
            writer: writer,
            applyId: applyId,
            boardId: itemBoard
        }
        //axios
        axios.post('/apply/v1/applyReplyAccept',data)
            .then(function (res) {
                console.log(res);
                window.alert("  !");
                setLoading(false);
                navigate('/'); // home 
            }).catch(function (err){
                window.alert("     !" + err);
                setLoading(false);
        });
    }


    return(
        <div>
            <div align="center" style={{padding : 100}}>
                <div >
                    <h1>  </h1>
                </div>
                <table className="table table-striped table-bordered table-hover">
                    <tbody>
                    <tr>
                        {/*TODO : td        */}
                        <th scope="row" > ID</th>
                        <td colSpan="6">iu1234</td>
                        {/*<td colSpan='6'>*/}
                        {/*    <input ref={writerRef} type="text" style={{width:200}}/>*/}
                        {/*</td>*/}
                    </tr>
                    <tr>
                        <th scope="row"> </th>
                        <td colSpan="3">
                            {writerEmail}
                        </td>
                    </tr>
                    <tr>
                        <th scope="row" ></th>
                        <td colSpan="3" width="23%">
                            <input ref={titleRef} type="text" placeholder=" " style={{width:300}}/>
                        </td>
                    </tr>
                    <tr>
                        <th scope="row"></th>
                        <td colSpan="6" className="view_text">
                            <textarea ref={contentRef} style={{width: 800, height: 300}}
                                      maxLength={1000}
                                      placeholder="        !! &#13;&#10;
                                      1.  (  ) &#13;&#10;
                                      2.        ! . &#13;&#10;
                                      (1000)">
                            </textarea>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </div>
            <div align="center">
                <button onClick={onClickApply} className="btn btn-warning" style={{backgroundColor: "#217Af0", width: 380, fontSize: 22}} >
                     
                </button>
            </div>
        </div>
    )
}

export default ApplyAccept;